	    -ms-user-select: none;
	        user-select: none;
}

.btn:hover {
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    opacity: 0.8;
}

.btn:active {
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
    opacity: 1;
}

.bar {
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    opacity: 0.1;
}

.bar > * {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.bar:hover {
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
    opacity: 0.5;
}

.bar:active {
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
    opacity: 0.8;
}

.btn-extend {
	-webkit-transition: all 0.5s;
	transition: all 0.5s;
}

.btn-extend:hover {
	-webkit-transition: all 0.5s;
	transition: all 0.5s;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

.btn-extend:active {
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
	-webkit-transform: translate(1.1);
	        transform: translate(1.1);
}

